import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.ahmedhnewa.landingpagedemo.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.contact.components.MyTextInputStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.about.AboutImageStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.about.AboutTextStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.portfolio.PortfolioSectionStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.portfolio.PortfolioArrowIconStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.testimonial.componenets.NavigationIconStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.service.ServiceCardStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.main.helloImTextStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.main.MainImageStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.main.compoments.MobileNavigation)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.main.compoments.NavigationItemStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.main.compoments.BrandingLogoStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.sections.main.compoments.SocialLinkItemStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.styles.MainButtonStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.landingpagedemo.components.BackToTopButtonStyle)
        com.ahmedhnewa.landingpagedemo.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Ahmed Hnewa")
        ) { com.ahmedhnewa.landingpagedemo.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
